:root,
html[data-theme=light] {
  box-sizing: border-box;
  --design-animation-default: all 0.2s ease-in-out;
  --border-radius: .3rem;
  --base-unit: 1rem;
  --font-text: "Inter",
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Helvetica,
    Arial,
    sans-serif,
    Apple Color Emoji,
    Segoe UI Emoji,
    Segoe UI Symbol;
  --font-headings: "Inter",
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Helvetica,
    Arial,
    sans-serif,
    Apple Color Emoji,
    Segoe UI Emoji,
    Segoe UI Symbol;
  --font-mono: "SFMono-Regular",
    Consolas,
    "Liberation Mono",
    Menlo,
    Courier,
    monospace;
}

:root {
  --color-background-default: #fff;
  --color-background-default-contrast: #161718;
  --color-text-primary: #0009;
  --color-text-primary-contrast: #e5e5e5;
  --color-headings-primary: #161718;
  --color-headings-primary-contrast: #fff;
  --color-link-primary: #3300f7;
  --color-link-hover: crimson;
  --color-link-current: black;
  --color-highlight-default: aliceblue;
  --color-highlight-extra: rgb(249 249 249);
  --color-highlight-warn: #f3e4e4;
  --color-highlight-friendly: linear-gradient(45deg, rgb(225 255 229), rgb(241 238 206));
  --color-highlight-friendly-text: #285428;
  --color-highlight-gray: #ebecec;
  --color-highlight-special: rgb(17 220 175 / 14%);
  --design-shadow-primary: rgba(84, 102, 120, 0.396) 0px 20px 30px -10px;
  --show-modifier-light: initial;
  --show-modifier-dark: none;
  transition: var(--design-animation-default);
  --widthMidi: calc(var(--base-unit)*70);
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  color: var(--color-text-primary);
  font-family: var(--font-text);
  font-weight: 400;
  font-style: normal;
  font-size: var(--base-unit);
  background: var(--color-background-default);
  hyphens: auto;
}

a {
  text-decoration: none;
}

h1 {
  font-size: calc(var(--base-unit)*1.8);
  font-weight: 300;
  color: var(--color-headings-primary);
  hyphens: none;
}

h2 {
  font-size: calc(var(--base-unit)*1.4);
  font-weight: 300;
}

p {
  line-height: 150%;
  color: var(--color-text-primary)
}

// Sections

nav {
  padding: calc(var(--base-unit)*1.5);
  display: flex;
  align-items: center;
  justify-content: space-between;

  img {
    height: 3rem;
  }

  ul {
    list-style-type: none;
    color: black;
    font-weight: 600;
    margin: 0;
    padding: 0;

    li {
      display: inline-block;

      &:not(:first-of-type) {
        margin-left: calc(var(--base-unit)*.8);
      }
    }

    a {
      color: black;
      text-decoration: underline;
      text-decoration-color: #161718;
      text-decoration-thickness: 2px;
    }
  }
}

.intro {
  padding: calc(var(--base-unit)*5.5) calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5);
  h1 {
    margin: 0;
  }
}

.spectrum {
  article {
    padding: calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5);
    margin: calc(var(--base-unit)*1.5) 0;
    h2 {
      margin: 0;
      margin-top: calc(var(--base-unit)*.5);
    }
    p {
      margin-top: calc(var(--base-unit)*.5);
    }
    ion-icon {
      font-size: calc(var(--base-unit)*3.5);
    }
    ul {
    margin: calc(var(--base-unit)*.5) 0;
      padding: 0;
      list-style-type: none;
    }
  }
}

.greentext {
  padding: calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5);
  h1 {
    margin: 0;
  }
  ion-icon {
      font-size: calc(var(--base-unit)*3.5);
      color: #a0d2a0;
  }
}
.about {
  padding: calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5) calc(var(--base-unit)*1.5);
  h1 {
    margin: 0;
  }
  ion-icon {
      font-size: calc(var(--base-unit)*3.5);
      color: #a0d2a0;
  }
}

// Buttons

button {
  padding: calc(var(--base-unit)*.5) calc(var(--base-unit)*.8);
  border-radius: 2px;
  border-style: solid;
  border-color: blue;
  background: blue;
  color: var(--color-headings-primary-contrast);
  font-weight: 600;
}

// Utilities

.u-align-center {
  display: inline-block;
  vertical-align: middle;
}

.u-theme-blacky {
    background: black;
    color: #e5e5e5;
    p {
    color: #fff9;
    }
    overflow: auto;
}